html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-image: url(../img/background.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  height: 100vh;
}
@media (min-width: 1024px) {
  .header {
    background-position: top center;
  }
}

.header__menu {
  display: block;
  text-align: center;
  padding: 2rem;
}
.header__menu i {
  color: #FFFFFF;
  font-size: 3rem;
  cursor: pointer;
}
@media (min-width: 1024px) {
  .header__menu {
    display: none;
  }
}

.header__nav {
  display: none;
}
@media (min-width: 1024px) {
  .header__nav {
    display: flex;
    justify-content: center;
    padding-top: 5rem;
    gap: 20rem;
  }
}

.header__nav-en {
  color: #FFFFFF;
  font-weight: 700;
  padding: 1rem 3rem;
  font-size: 2rem;
}
.header__nav-en:hover {
  background-color: #FFFFFF;
  color: #553F6F;
  padding: 1rem 3rem;
  border-radius: 1rem;
}

.header__title {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 80%;
  text-transform: uppercase;
  color: #FFFFFF;
}
.header__title img {
  width: 85%;
}
@media (min-width: 1024px) {
  .header__title img {
    width: 55%;
  }
}
.header__title .header__title-name {
  text-transform: capitalize;
  font-size: 2.4rem;
  margin: 0;
  font-weight: bold;
}
@media (min-width: 1024px) {
  .header__title .header__title-name {
    font-size: 4rem;
  }
}
.header__title .header__title-pro {
  font-weight: 300;
  text-transform: capitalize;
  margin: 0;
  font-style: oblique;
  font-size: 2rem;
}
@media (min-width: 1024px) {
  .header__title .header__title-pro {
    font-size: 3rem;
  }
}

.overlay {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: rgba(85, 63, 111, 0.9);
  overflow-x: hidden;
  transition: 0.5s;
}
.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: #FFFFFF;
  display: block;
  transition: 0.3s;
}
.overlay a:hover, .overlay a:focus {
  color: #f5f5f5;
}
.overlay .closebtn {
  position: absolute;
  top: 20px;
  left: 48%;
  font-size: 60px;
}

.overlay-content {
  position: relative;
  top: 25%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

@media screen and (max-height: 450px) {
  .overlay a {
    font-size: 20px;
  }

  .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
}
.aboutme__head {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5rem 0;
  gap: 1rem;
}
.aboutme__head img {
  width: 5rem;
}

.aboutme__title {
  color: #FDC93A;
  font-weight: bold;
  font-size: 4rem;
}
@media (min-width: 1024px) {
  .aboutme__title {
    font-size: 5rem;
  }
}

.aboutme__body {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
@media (min-width: 1024px) {
  .aboutme__body {
    flex-direction: row;
  }
}

.aboutme__picture {
  overflow: hidden;
  width: 26rem;
  border-radius: 50%;
  margin: 0 auto;
}

.aboutme__text {
  max-width: 90%;
  width: 90%;
  margin: 0 auto;
}

.portfolio__head {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5rem 0;
  gap: 1rem;
}
.portfolio__head img {
  width: 5rem;
}

.portfolio__title {
  color: #FDC93A;
  font-weight: bold;
  font-size: 4rem;
}
@media (min-width: 1024px) {
  .portfolio__title {
    font-size: 5rem;
  }
}

.portfolio__body {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
@media (min-width: 1024px) {
  .portfolio__body {
    flex-direction: row;
  }
}

.portfolio__grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 5rem;
}
@media (min-width: 768px) {
  .portfolio__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .portfolio__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.portfolio__card {
  border: 1px solid #e1e1e1;
  padding: 2rem;
  border-radius: 1rem;
  -webkit-box-shadow: 0px -2px 31px -6px rgba(0, 0, 0, 0.38);
  -moz-box-shadow: 0px -2px 31px -6px rgba(0, 0, 0, 0.38);
  box-shadow: 0px -2px 31px -6px rgba(0, 0, 0, 0.38);
}

.porfolio__card-img {
  overflow: hidden;
  border-radius: 2rem;
}
.porfolio__card-img img {
  width: 33rem;
  height: 33rem;
  object-fit: cover;
  margin: 0 auto;
  border-radius: 2rem;
  transition: transform 0.3s ease-in-out;
  cursor: pointer;
}
.porfolio__card-img img:hover {
  transform: scale(1.1);
}

.portfolio__card-title {
  margin: 1rem auto;
  color: #FF355A;
  font-size: 3rem;
  text-align: center;
  font-weight: bold;
}

.portfolio__card-text {
  margin: 1rem;
  text-align: justify;
}

.portfolio__card-tags {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 2rem;
  margin: 1rem;
}
.portfolio__card-tags .tags {
  padding: 0.5rem 1rem;
  border: 2px solid #553F6F;
  border-radius: 2rem;
  color: #553F6F;
}
.portfolio__card-tags .tags:hover {
  cursor: pointer;
  background-color: #553F6F;
  color: #FFFFFF;
}